'use client';
import ProtectedRoute from '@/components/ProtectedRoute';
import { useDashboardLogic } from './dashboardLogic';
import { useState } from 'react';
import { LockClosedIcon, Bars3Icon, XMarkIcon } from '@heroicons/react/24/outline';

export default function DashboardPage() {
    const {
        user,
        notes,
        newTitle,
        setNewTitle,
        newContent,
        setNewContent,
        editingNoteId,
        shareUserId,
        setShareUserId,
        sharePermission,
        setSharePermission,
        friends,
        friendRequests,
        friendRequestUsername,
        setFriendRequestUsername,
        loading,
        message,
        error,
        handleLogout,
        handleRequestVerification,
        handleCreateNote,
        handleEditNote,
        handleUpdateNote,
        handleDeleteNote,
        handleShareNote,
        handleSendFriendRequest,
        handleRespondToFriendRequest,
        isOwner,
    } = useDashboardLogic();

    const [isSidebarOpen, setIsSidebarOpen] = useState(true); // Sidebar toggle state
    const [activeTab, setActiveTab] = useState('notes'); // Active content tab

    if (loading) return <div className="text-center mt-10">Loading...</div>;

    // Component for Friends section
    const FriendsSection = () => (
        <div className="border border-gray-200 dark:border-gray-700 p-4 rounded">
            <h2 className="text-xl font-semibold mb-2 text-gray-900 dark:text-gray-100">Friends</h2>
            {user.user.verified ? (
                <>
                    <div className="mb-4">
                        <input
                            value={friendRequestUsername}
                            onChange={(e) => setFriendRequestUsername(e.target.value)}
                            placeholder="Enter username to send friend request"
                            className="p-2 border border-gray-200 dark:border-gray-700 rounded mr-2 bg-gray-100 dark:bg-gray-700 text-gray-900 dark:text-gray-100"
                        />
                        <button
                            onClick={handleSendFriendRequest}
                            className="bg-white dark:bg-slate-600 border border-gray-300 dark:border-none text-gray-900 dark:text-gray-100 px-5 py-2.5 rounded hover:bg-gray-100 dark:hover:bg-slate-400 transition duration-200 text-base"
                        >
                            Send Friend Request
                        </button>
                    </div>
                    <div className="mb-6">
                        <h3 className="text-lg font-medium mb-2 text-gray-900 dark:text-gray-100">Your Friends</h3>
                        {friends.length > 0 ? (
                            <ul className="space-y-1">
                                {friends.map((friend, index) => (
                                    <li
                                        key={friend._id || `friend-${index}`}
                                        className="p-2 bg-gray-100 dark:bg-gray-700 rounded text-gray-900 dark:text-gray-100"
                                    >
                                        {friend.username}
                                    </li>
                                ))}
                            </ul>
                        ) : (
                            <p className="text-gray-500 dark:text-gray-400">No friends yet.</p>
                        )}
                    </div>
                    <div>
                        <h3 className="text-lg font-medium mb-2 text-gray-900 dark:text-gray-100">Friend Request Logs</h3>
                        {friendRequests.length > 0 ? (
                            <div className="space-y-4">
                                <div>
                                    <h4 className="text-md font-semibold text-gray-700 dark:text-gray-300">Incoming Requests</h4>
                                    {friendRequests.filter(r => r.receiver._id === user.user._id).length > 0 ? (
                                        <ul className="space-y-2">
                                            {friendRequests
                                                .filter(r => r.receiver._id === user.user._id)
                                                .map((request, index) => (
                                                    <li
                                                        key={request._id || `incoming-${index}`}
                                                        className="p-2 border border-gray-200 dark:border-gray-700 rounded flex justify-between items-center text-gray-900 dark:text-gray-100"
                                                    >
                                                        <span>
                                                            {request.status === 'pending' &&
                                                                `${request.sender.username} sent you a friend request on ${new Date(request.createdAt).toLocaleString()} - Awaiting your response`}
                                                            {request.status === 'accepted' &&
                                                                `${request.sender.username}'s friend request was accepted on ${new Date(request.updatedAt).toLocaleString()}`}
                                                            {request.status === 'rejected' &&
                                                                `${request.sender.username}'s friend request was rejected on ${new Date(request.updatedAt).toLocaleString()}`}
                                                        </span>
                                                        {request.status === 'pending' && (
                                                            <div className="space-x-2">
                                                                <button
                                                                    onClick={() => handleRespondToFriendRequest(request._id, 'accept')}
                                                                    className="bg-white dark:bg-slate-600 border border-gray-300 dark:border-none text-gray-900 dark:text-gray-100 px-2 py-1 rounded hover:bg-gray-100 dark:hover:bg-slate-400 transition duration-200"
                                                                >
                                                                    Accept
                                                                </button>
                                                                <button
                                                                    onClick={() => handleRespondToFriendRequest(request._id, 'reject')}
                                                                    className="bg-white dark:bg-slate-600 border border-gray-300 dark:border-none text-gray-900 dark:text-gray-100 px-2 py-1 rounded hover:bg-gray-100 dark:hover:bg-slate-400 transition duration-200"
                                                                >
                                                                    Reject
                                                                </button>
                                                            </div>
                                                        )}
                                                    </li>
                                                ))}
                                        </ul>
                                    ) : (
                                        <p className="text-gray-500 dark:text-gray-400">No incoming requests.</p>
                                    )}
                                </div>
                                <div>
                                    <h4 className="text-md font-semibold text-gray-700 dark:text-gray-300">Outgoing Requests</h4>
                                    {friendRequests.filter(r => r.sender._id === user.user._id).length > 0 ? (
                                        <ul className="space-y-2">
                                            {friendRequests
                                                .filter(r => r.sender._id === user.user._id)
                                                .map((request, index) => (
                                                    <li
                                                        key={request._id || `outgoing-${index}`}
                                                        className="p-2 border border-gray-200 dark:border-gray-700 rounded text-gray-900 dark:text-gray-100"
                                                    >
                                                        {request.status === 'pending' &&
                                                            `You sent a friend request to ${request.receiver.username} on ${new Date(request.createdAt).toLocaleString()} - Awaiting their response`}
                                                        {request.status === 'accepted' &&
                                                            `Your friend request to ${request.receiver.username} was accepted on ${new Date(request.updatedAt).toLocaleString()}`}
                                                        {request.status === 'rejected' &&
                                                            `${request.receiver.username} rejected your friend request on ${new Date(request.updatedAt).toLocaleString()}`}
                                                    </li>
                                                ))}
                                        </ul>
                                    ) : (
                                        <p className="text-gray-500 dark:text-gray-400">No outgoing requests.</p>
                                    )}
                                </div>
                            </div>
                        ) : (
                            <p className="text-gray-500 dark:text-gray-400">No friend request activity.</p>
                        )}
                    </div>
                </>
            ) : (
                <p className="text-yellow-600 dark:text-yellow-400 mb-2">Verify your email to send or receive friend requests.</p>
            )}
        </div>
    );

    // Component for Notes section
    const NotesSection = () => (
        <div className="border border-gray-200 dark:border-gray-700 p-4 rounded">
            <h2 className="text-xl font-semibold mb-2 text-gray-900 dark:text-gray-100">Your Notes</h2>
            {user.user.verified ? (
                <>
                    <input
                        value={newTitle}
                        onChange={(e) => setNewTitle(e.target.value)}
                        placeholder="Note title"
                        className="w-full p-2 border border-gray-200 dark:border-gray-700 rounded mb-2 bg-gray-100 dark:bg-gray-700 text-gray-900 dark:text-gray-100"
                    />
                    <textarea
                        value={newContent}
                        onChange={(e) => setNewContent(e.target.value)}
                        placeholder="Write a new note..."
                        className="w-full p-2 border border-gray-200 dark:border-gray-700 rounded mb-2 bg-gray-100 dark:bg-gray-700 text-gray-900 dark:text-gray-100"
                    />
                    <button
                        onClick={editingNoteId ? handleUpdateNote : handleCreateNote}
                        className="bg-white dark:bg-slate-600 border border-gray-300 dark:border-none text-gray-900 dark:text-gray-100 px-5 py-2.5 rounded hover:bg-gray-100 dark:hover:bg-slate-400 transition duration-200 text-base"
                    >
                        {editingNoteId ? 'Update Note' : 'Add Note'}
                    </button>
                    {message && <p className="text-green-500 dark:text-green-400 text-sm mt-2">{message}</p>}
                    {error && <p className="text-red-500 dark:text-red-400 text-sm mt-2">{error}</p>}
                    {notes.length > 0 ? (
                        <ul className="mt-4 space-y-4">
                            {notes.map((note, index) => (
                                <li key={note._id || `note-${index}`} className="border border-gray-200 dark:border-gray-700 p-2 rounded">
                                    <div className="flex justify-between items-center">
                                        <div>
                                            <strong className="text-gray-900 dark:text-gray-100">{note.title}</strong>: <span className="text-gray-900 dark:text-gray-100">{note.content}</span>
                                            <span className="text-gray-500 dark:text-gray-400 text-sm">
                                                {' '}
                                                ({new Date(note.createdAt).toLocaleString()})
                                            </span>
                                            {typeof note.owner !== 'string' && note.owner._id !== user.user._id && (
                                                <p className="text-gray-600 dark:text-gray-400 text-sm">Shared by {note.owner.username}</p>
                                            )}
                                        </div>
                                        {isOwner(note, user.user._id) && (
                                            <div className="space-x-2">
                                                <button
                                                    onClick={() => handleEditNote(note)}
                                                    className="bg-white dark:bg-slate-600 border border-gray-300 dark:border-none text-gray-900 dark:text-gray-100 px-2 py-1 rounded hover:bg-gray-100 dark:hover:bg-slate-400 transition duration-200"
                                                >
                                                    Edit
                                                </button>
                                                <button
                                                    onClick={() => handleDeleteNote(note._id)}
                                                    className="bg-white dark:bg-slate-600 border border-gray-300 dark:border-none text-gray-900 dark:text-gray-100 px-2 py-1 rounded hover:bg-gray-100 dark:hover:bg-slate-400 transition duration-200"
                                                >
                                                    Delete
                                                </button>
                                            </div>
                                        )}
                                    </div>
                                    {isOwner(note, user.user._id) && (
                                        <div className="mt-2">
                                            <input
                                                value={shareUserId}
                                                onChange={(e) => setShareUserId(e.target.value)}
                                                placeholder="User ID to share with"
                                                className="p-1 border border-gray-200 dark:border-gray-700 rounded mr-2 bg-gray-100 dark:bg-gray-700 text-gray-900 dark:text-gray-100"
                                            />
                                            <select
                                                value={sharePermission}
                                                onChange={(e) => setSharePermission(e.target.value as 'viewer' | 'editor')}
                                                className="p-1 border border-gray-200 dark:border-gray-700 rounded mr-2 bg-gray-100 dark:bg-gray-700 text-gray-900 dark:text-gray-100"
                                            >
                                                <option value="viewer">Viewer</option>
                                                <option value="editor">Editor</option>
                                            </select>
                                            <button
                                                onClick={() => handleShareNote(note._id)}
                                                className="bg-white dark:bg-slate-600 border border-gray-300 dark:border-none text-gray-900 dark:text-gray-100 px-2 py-1 rounded hover:bg-gray-100 dark:hover:bg-slate-400 transition duration-200"
                                            >
                                                Share
                                            </button>
                                        </div>
                                    )}
                                    {note.sharedWith.length > 0 && (
                                        <div className="mt-2 text-sm text-gray-600 dark:text-gray-400">
                                            Shared with:{' '}
                                            {note.sharedWith
                                                .map((entry) => `${entry.user.username} (${entry.permission})`)
                                                .join(', ')}
                                        </div>
                                    )}
                                </li>
                            ))}
                        </ul>
                    ) : (
                        <p className="text-gray-500 dark:text-gray-400 mt-2">No notes yet.</p>
                    )}
                </>
            ) : (
                <div className="bg-yellow-100 dark:bg-yellow-900/20 p-4 rounded">
                    <p className="mb-2 text-yellow-800 dark:text-yellow-400">Your email is not verified. Limited access (1 note max).</p>
                    <button
                        onClick={handleRequestVerification}
                        className="bg-white dark:bg-slate-600 border border-gray-300 dark:border-none text-gray-900 dark:text-gray-100 px-5 py-2.5 rounded hover:bg-gray-100 dark:hover:bg-slate-400 transition duration-200 text-base"
                    >
                        Request Verification
                    </button>
                    {error && <p className="text-red-500 dark:text-red-400 text-sm mt-2">{error}</p>}
                    {message && <p className="text-green-500 dark:text-green-400 text-sm mt-2">{message}</p>}
                    {notes.length === 0 && (
                        <div className="mt-4">
                            <input
                                value={newTitle}
                                onChange={(e) => setNewTitle(e.target.value)}
                                placeholder="Note title"
                                className="w-full p-2 border border-gray-200 dark:border-gray-700 rounded mb-2 bg-gray-100 dark:bg-gray-700 text-gray-900 dark:text-gray-100"
                            />
                            <textarea
                                value={newContent}
                                onChange={(e) => setNewContent(e.target.value)}
                                placeholder="Write your note (1 note max)..."
                                className="w-full p-2 border border-gray-200 dark:border-gray-700 rounded mb-2 bg-gray-100 dark:bg-gray-700 text-gray-900 dark:text-gray-100"
                            />
                            <button
                                onClick={handleCreateNote}
                                className="bg-white dark:bg-slate-600 border border-gray-300 dark:border-none text-gray-900 dark:text-gray-100 px-5 py-2.5 rounded hover:bg-gray-100 dark:hover:bg-slate-400 transition duration-200 text-base"
                            >
                                Add Note
                            </button>
                        </div>
                    )}
                    {notes.length > 0 && (
                        <ul className="mt-4 space-y-2">
                            {notes.map((note, index) => (
                                <li key={note._id || `unverified-note-${index}`} className="border border-gray-200 dark:border-gray-700 p-2 rounded">
                                    <strong className="text-gray-900 dark:text-gray-100">{note.title}</strong>: <span className="text-gray-900 dark:text-gray-100">{note.content}</span>
                                    <span className="text-gray-500 dark:text-gray-400 text-sm">
                                        {' '}
                                        ({new Date(note.createdAt).toLocaleString()})
                                    </span>
                                    {typeof note.owner !== 'string' && note.owner._id !== user.user._id && (
                                        <p className="text-gray-600 dark:text-gray-400 text-sm">Shared by {note.owner.username}</p>
                                    )}
                                </li>
                            ))}
                        </ul>
                    )}
                </div>
            )}
        </div>
    );

    // Placeholder for Account Settings
    const AccountSettingsSection = () => (
        <div className="border border-gray-200 dark:border-gray-700 p-4 rounded">
            <h2 className="text-xl font-semibold mb-2 text-gray-900 dark:text-gray-100">Account Settings</h2>
            <p className="text-gray-500 dark:text-gray-400">Coming soon...</p>
        </div>
    );

    // Placeholder for Notifications
    const NotificationsSection = () => (
        <div className="border border-gray-200 dark:border-gray-700 p-4 rounded">
            <h2 className="text-xl font-semibold mb-2 text-gray-900 dark:text-gray-100">Notifications</h2>
            <p className="text-gray-500 dark:text-gray-400">No notifications yet.</p>
        </div>
    );

    return (
        <ProtectedRoute>
            <div className="flex min-h-screen">
                {/* Sidebar */}
                <div
                    className={`fixed top-0 left-0 h-full bg-gray-100 dark:bg-gray-800 border-r border-gray-200 dark:border-gray-700 transition-all duration-300 ${
                        isSidebarOpen ? 'w-64' : 'w-0'
                    } overflow-hidden`}
                >
                    <div className="p-4">
                        {/* Secure Note at the top of sidebar */}
                        <div className="flex items-center text-gray-900 dark:text-gray-100 text-2xl font-bold mb-6">
                            <LockClosedIcon className="h-8 w-8 mr-2 text-gray-400" />
                            Secure Note
                        </div>
                        {/* Sidebar Menu */}
                        <ul className="space-y-2">
                            <li>
                                <button
                                    onClick={() => setActiveTab('friends')}
                                    className={`w-full text-left p-2 rounded text-gray-900 dark:text-gray-100 hover:bg-gray-200 dark:hover:bg-gray-700 ${
                                        activeTab === 'friends' ? 'bg-gray-200 dark:bg-gray-700' : ''
                                    }`}
                                >
                                    Friends
                                </button>
                            </li>
                            <li>
                                <button
                                    onClick={() => setActiveTab('notes')}
                                    className={`w-full text-left p-2 rounded text-gray-900 dark:text-gray-100 hover:bg-gray-200 dark:hover:bg-gray-700 ${
                                        activeTab === 'notes' ? 'bg-gray-200 dark:bg-gray-700' : ''
                                    }`}
                                >
                                    Notes
                                </button>
                            </li>
                            <li>
                                <button
                                    onClick={() => setActiveTab('account-settings')}
                                    className={`w-full text-left p-2 rounded text-gray-900 dark:text-gray-100 hover:bg-gray-200 dark:hover:bg-gray-700 ${
                                        activeTab === 'account-settings' ? 'bg-gray-200 dark:bg-gray-700' : ''
                                    }`}
                                >
                                    Account Settings
                                </button>
                            </li>
                            <li>
                                <button
                                    onClick={() => setActiveTab('notifications')}
                                    className={`w-full text-left p-2 rounded text-gray-900 dark:text-gray-100 hover:bg-gray-200 dark:hover:bg-gray-700 ${
                                        activeTab === 'notifications' ? 'bg-gray-200 dark:bg-gray-700' : ''
                                    }`}
                                >
                                    Notifications
                                </button>
                            </li>
                        </ul>
                    </div>
                </div>

                {/* Main Content */}
                <div
                    className={`flex-1 transition-all duration-300 ${
                        isSidebarOpen ? 'ml-64' : 'ml-0'
                    } p-8`}
                >
                    {/* Sidebar Toggle Button */}
                    <button
                        onClick={() => setIsSidebarOpen(!isSidebarOpen)}
                        className="mb-6 p-2 rounded-full bg-gray-200 dark:bg-gray-700 text-gray-900 dark:text-gray-100 hover:bg-gray-300 dark:hover:bg-gray-600"
                    >
                        {isSidebarOpen ? <XMarkIcon className="h-6 w-6" /> : <Bars3Icon className="h-6 w-6" />}
                    </button>

                    {/* Dynamic Content */}
                    <div className="bg-white dark:bg-gray-800 p-8 rounded-2xl shadow-[0_0_10px_rgba(0,0,0,0.05)] dark:shadow-[0_0_10px_rgba(255,255,255,0.1)] border border-gray-200 dark:border-gray-700 w-full max-w-4xl mx-auto">
                        <div className="flex justify-between items-center mb-6">
                            <h1 className="text-4xl font-bold tracking-wide text-gray-900 dark:text-gray-100">Dashboard</h1>
                            <p className="text-lg text-gray-700 dark:text-gray-300">Welcome, {user.user.username}!</p>
                        </div>
                        {activeTab === 'friends' && <FriendsSection />}
                        {activeTab === 'notes' && <NotesSection />}
                        {activeTab === 'account-settings' && <AccountSettingsSection />}
                        {activeTab === 'notifications' && <NotificationsSection />}
                    </div>
                </div>
            </div>
        </ProtectedRoute>
    );
}